@font-face {
    font-family: glitch;
    src: url(LoveGlitch.ttf);
}

#Game-Title {
    font-family: glitch;
    font-size: 20vw;
    position: fixed;
    top: 5vh;
    left: 50%;
    width: fit-content;
    transform: translate(-50%);
}

.choose{
    background: blue;
    font-size: large;
    border: 3px grey solid;
    border-radius: 5px;
    color: white;
    padding: 2vh 3vw;
    cursor: pointer;
    font-family: fangsong;
    font-weight: 900;
    text-decoration: none;
}

#Main-Game-Options {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
#HomePage {
    background: url(https://t3.ftcdn.net/jpg/01/61/92/74/360_F_161927465_u7G2W2fbMvWReTtC9G6ijR8qXg1V0zhl.jpg);
    background-size: contain;
    height: 100vh;
    width: 100vw;
    position: fixed;
    background-repeat: no-repeat;
    /* background: url(https://t3.ftcdn.net/jpg/01/61/92/74/360_F_161927465_u7G2W2fbMvWReTtC9G6ijR8qXg1V0zhl.jpg); */
    background-repeat: round;
}
div#root {
    display: none;
    font-family: Helvetica, sans-serif;
    overflow: hidden;
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: 100vh;
    width: 100vw;
    position: fixed;
    display: block;
    background-size: cover;
}


