#PlaygroundSvg {
    background: url(./images/pgscene1.jpg);
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-repeat: round;
    cursor: url(./images/net.png),auto;
}

div#score {
    /* height: 20px; */
    position: absolute;
    color: white;
    border: 1px dashed black;
    padding: 5px;
    border-radius: 2px;
    left: 1%;
    top: 1%;
    font-size: x-large;
    font-weight: 900;
}
.plus1 {
    /* height: 20px; */
    position: absolute;
    color: #292ec3;
    border: 1px dashed #94d73b;
    padding: 5px;
    border-radius: 2px;
    font-size: large;
    font-weight: 900;
    background: white;
    border-radius: 40%;
}
.Bee{
    transition: 1s linear all;
}

#Time {
    position: absolute;
    right: 0;
    color: white;
    font-size: x-large;
    border: 1px #68bf26 dashed;
    padding: 5px;
    top: 1%;
    right: 1%;
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    gap: 5px;
    border-radius: 2px;
    font-weight: 900;
}

.catchingNet{
    opacity: 1;
    /* transition: 1s linear all; */
}

#Startbtn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #dd9b19;
    padding: 5vh 5vw;
    font-size: x-large;
    border-radius: 5px;
    color: cornsilk;
    cursor: pointer;
}

div#AfterGameFinsih {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% ,-50%);
    background-color: #85b430;
    display: grid;
    padding: 5vh 5vw;
    gap: 10px;
    border-radius: 10px;
    display: none;
    font-size: xx-large;
}

#Game-Over{
    color: red;
}

#Play-Again {
    background: #1c1c8d;
    color: white;
    padding: 2vh 2vw;
    border: navajowhite;
    border-radius: 4px;
    font-size: larger;
    cursor: pointer;
}


#Your-Score {
    color: aliceblue;
    border: 2px ridge blue;
    padding: 1vh 1vw;
    border-radius: 10px;
}